@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');
/*color*/
:root {
    --color-blue: #2196F3;
    --color-grey: #A0B0B9;
}

/*text style*/
.--os-sb-p12 {
    font-family: 'Open Sans';
    font-size: 12px;
    font-weight: 600;
}

.--os-sb-p14 {
    font-family: 'Open Sans';
    font-size: 14px;
    font-weight: 600;
}

.--os-sb-p24 {
    font-family: 'Open Sans';
    font-size: 24px;
    font-weight: 600;
}

body {
    background: #E5E5E5;
    padding: 50px 0;
    font-family: 'Open Sans', sans-serif;
}


.container {
    width: 502px;
    padding: 0 15px;
    margin: 0 auto;
}
/*aside start*/
.aside {
    width: 267px;
}

.transfer-filter, .company-filter {
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    margin-top: 20px;
}

.company-filter__title,
.transfer-filter__title {
    margin-bottom: 20px;
    text-transform: uppercase;
}

.--input-hover {
    font-size: 13px;
    font-weight: 400;
}

.--input-hover:hover {
    background: rgba(241, 252, 255, 1);
}


label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    padding-left: 25px;
    margin-right: 15px;
    font-size: 13px;
}



input[type=radio],
input[type=checkbox] {
    display: none;
}

.radio {
    margin-bottom: 10px;
}

label:before {
    content: "";
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 10px;
    position: absolute;
    left: 0;
    bottom: 1px;
    background-color: #fff;
    box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, .3), 0px 1px 0px 0px rgba(255, 255, 255, .8);
}

.radio label:before {
    border-radius: 50%;
    color: #2196F3;
}

.checkbox label {
    margin-bottom: 10px;
}

.checkbox label:before {
    border-radius: 2px;
}

input[type=radio]:checked+label:before {
    content: "\2022";
    width: 20px;
    height: 20px;
    color:#2196F3;
    border: 1px solid #2196F3;
    font-size: 40px;
    text-align: center;
    line-height: 15px;
}

input[type=checkbox]:checked+label:before {
    content: "\2713";
    width: 20px;
    height: 20px;
    color:#2196F3;
    border: 1px solid #2196F3;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
    font-size: 15px;
    text-align: center;
    line-height: 15px;
}

/*aside end*/


/*style for section promotion start*/

.filter-price__link {
    padding: 15px; 
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    color: #000;
    border: 1px solid #DFE5EC;
    background: #ffffff;

    &:hover {
        background: #F1FCFF;
    }


    &:active {
        background: var(--color-blue);
        color: #fff;
    } 

    &-active {
        background: var(--color-blue);
        color: #fff;
    }
   
}


.card {
    margin-top: 30px;
    background: #ffffff;
    padding: 20px;
    border-radius: 5px;

    &__price {
        color: var(--color-blue);
    }
}

.info {
    margin-top: 26px;
}

.info__discription {
    color: var(--color-grey);
}

.anime {
    transform: scale(0);
    opacity: 0;
}

.hide {
    display: none;
}

@keyframes hide {
  0% {
    visibility: visible;

  }

  100% {
        visibility: hidden;
  }
}

.button__load {
    background: var(--color-blue);
    color: #ffffff;
    padding: 15px 20px;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    width: 472px;
    height: 50px;
    cursor: pointer;

    &:hover {
        background: #ffffff;
        color: #000;
    }
}
/*style for section promotion end*/

 